import React, { Component } from 'react';
import axios from 'axios';
import { connect } from 'react-redux';
import { COMMENTS_CALL_REQUEST } from '../../Const/const';

class Comment extends Component {
    state = {
        data: []
    }
    componentWillMount() {
        axios.get(`https://jsonplaceholder.typicode.com/comments?postId=${this.props.id}`)
            .then(response => this.setState({data: response.data}))
       // this.props.fetchCom();
    }
    render() {
       // const { comData } = this.props
        return(
            <div className='item'>
                {/* {console.log(comData)} */}
                {this.state.data.map(item => (
                    <div key={Math.random()}>
                        <p >Name: {item.name}</p>
                        <span >Body: {item.body}</span>
                        <hr />
                    </div>
                ))}
            </div>
        )
    }
}

// const mapStateToProps = state => {
//     return {
//         comData: state.init.ComData
//     }
// }

const mapDispatchToProps = dispatch => {
    return {
        fetchCom: () => dispatch({type: COMMENTS_CALL_REQUEST})
    }
}

export default connect(null, mapDispatchToProps)(Comment);


